@import url("https://fonts.googleapis.com/css?family=Raleway:100i,200i");
@import url("https://fonts.googleapis.com/css?family=Lato");
@import url("https://fonts.googleapis.com/css?family=Roboto");
@import url("https://fonts.googleapis.com/css?family=Kavivanar");
@import url("https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,200i,300,300i,400,400i,600,600i,700,700i,900,900i");
.App {
  text-align: center; }

body {
  margin: 0;
  padding: 0;
  font-family: 'Lato';
  background-color: #ddd; }
  body.in-transition {
    -webkit-transition: -webkit-transform 900ms ease;
    transition: -webkit-transform 900ms ease;
    -o-transition: transform 900ms ease;
    transition: transform 900ms ease;
    transition: transform 900ms ease, -webkit-transform 900ms ease; }

h1, h2, h3 {
  margin-top: 0; }

h1 {
  color: #1e836c;
  font-size: 280%;
  font-weight: 300;
  font-family: 'Source Sans Pro', sans-serif; }

h2 {
  color: #45b29a;
  font-size: 170%;
  font-weight: 300;
  text-align: left;
  font-family: 'Source Sans Pro', sans-serif; }

h3 {
  color: #45b29a;
  font-weight: 400;
  font-size: 110%;
  text-align: left;
  font-family: 'Source Sans Pro', sans-serif; }

.secondary h1 {
  color: #F05F40; }

.secondary h2 {
  color: #F05F40;
  text-align: center; }

.secondary h3 {
  color: #f1846d;
  font-size: 125%;
  font-weight: 300; }

p {
  font-family: 'Open Sans';
  line-height: 180%;
  color: #666;
  text-align: left; }

img {
  -webkit-transition: height 0.3s, width 0.3s;
  -o-transition: height 0.3s, width 0.3s;
  transition: height 0.3s, width 0.3s; }

.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 20px 0px;
  -webkit-transition: background-color 0.5s, padding 0.3s, -webkit-box-shadow 0.3s;
  transition: background-color 0.5s, padding 0.3s, -webkit-box-shadow 0.3s;
  -o-transition: background-color 0.5s, padding 0.3s, box-shadow 0.3s;
  transition: background-color 0.5s, padding 0.3s, box-shadow 0.3s;
  transition: background-color 0.5s, padding 0.3s, box-shadow 0.3s, -webkit-box-shadow 0.3s; }

.column {
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center; }

.page-section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 60px 0px;
  background-color: #fff; }

.cover-img {
  max-width: 100%;
  height: 230px;
  position: relative;
  top: 500px;
  background-image: url("./images/simple_cover3.png");
  background-size: auto 100%;
  background-position: center;
  background-repeat: no-repeat; }
  .cover-img img {
    max-width: 100%;
    min-width: 100%; }

.martin {
  position: relative;
  top: -300px;
  padding-bottom: 1px;
  padding-top: 20px;
  background-image: url("./images/clouds.png");
  background-size: auto 100%;
  background-position: center;
  background-repeat: no-repeat; }
  .martin h1 {
    color: #0173AD;
    font-size: 400%;
    font-family: 'Raleway', sans-serif;
    font-weight: 100; }
  .martin h3 {
    text-align: center;
    color: #0173AD; }

.footer {
  padding-top: 40px;
  background-color: #333;
  padding-bottom: 0px;
  position: relative;
  z-index: 1; }
  .footer div {
    -ms-flex-direction: row;
        flex-direction: row;
    width: 650px;
    max-width: 95%; }
    .footer div h1, .footer div h2, .footer div h3 {
      color: #666;
      margin-bottom: 0px; }
    .footer div p {
      color: #777;
      margin-top: 10px;
      text-align: center; }

.active {
  color: #0173AD !important;
  text-decoration: underline !important; }



